<template>
  <div ref="chart3" style="width:600px; height: 376px;"></div>
</template>

<script>
import cityJson from '../city.json';
export default {
  data() {
    return {};
  },
  mounted() {
    this.getEchartData();
  },
  methods: {
    getEchartData() {
      var myChart = this.$echarts.init(this.$refs.chart3);
       this.$echarts.registerMap('china', {geoJSON: cityJson});
      console.log(cityJson);
      myChart.setOption({
        geo:{
          type:"map",
          map:"china",
          zoom:1.2,
        },
        label:{
          show: true,
        }
      });
      window.onresize = function () {
        myChart.resize();
      };
      // this.$on("hook:destroyed", () => {
      //   window.removeEventListener("resize", function () {
      //     myChart.resize();
      //   });
      // });
    },
  },
};
</script>
